// Core
import React from 'react';

// Instruments
import styles from './FriendList.module.css';

// Components
import Friend from './Friend';

const FriendList = ({ friends }) => (
  <ul className={styles.list}>
    {friends.map(friend => (
      <Friend friend={friend} key={friend.id} />
    ))}
  </ul>
);

export default FriendList;
